nav {
  background-color: rgba(0, 0, 0, 0);
}

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 50px;
  font-size: 1.5rem;
  z-index: 9999;
  margin: 0;
}

nav ul {
  float: right;
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  padding-right: 5%;
}

nav ul {
  list-style: rgba(0, 0, 0, 0);
}

nav ul a {
  list-style: 80px;
  color: #fff;
  padding: 23px 20px;
  padding-bottom: 5px;
  text-decoration: none;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
}

nav ul a {
  background-color: none;
}

nav ul a:hover,
nav ul a.active {
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(0, 0, 0, 0)),
    color-stop(rgba(0, 0, 0, 0)),
    color-stop(rgba(0, 0, 0, 0)),
    color-stop(rgba(0, 0, 0, 0)),
    color-stop(rgba(0, 0, 0, 0)),
    to(rgba(109, 222, 184, 0.8))
  );
  background: -webkit-linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(109, 222, 184, 0.8)
  );
  background: -o-linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(109, 222, 184, 0.8)
  );
  background: linear-gradient(
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(109, 222, 184, 0.8)
  );
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  -webkit-transition: all 0.3s ease;
  color: #90ee90;
  text-shadow: 0 0 px rgba(143, 243, 201, 1);
}

nav.shrink {
  width: 100vw;
  -webkit-transition: all 0 ease;
  -o-transition: all 0 ease;
  transition: all 0 ease;
  background: rgba(0, 0, 0, 0.8);
  font-size: 1.2rem;
  height: 53px;
}

@media only screen and (max-width: 992px) {
  nav {
    position: fixed;
    -ms-flex-pack: center;
        justify-content: center;
  }
}

/* Responsive Device Resolution : Mobile */
@media only screen and (max-width: 536px) {
  nav {
    font-size: 1.3rem;
  }

  nav ul a {
    padding: 15px 13px;
  }
  nav.shrink {
    font-size: 1.2rem;
    height: 53px;
  }
}

@media only screen and (max-width: 320px) {
  nav {
    font-size: 1.1rem;
  }
  nav {
    font-size: 1.1rem;
  }

  nav ul a {
    padding: 13px 10px;
  }
  nav.shrink {
    font-size: 1rem;
    height: 45px;
  }
}



